@import url("https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Urbanist:ital,wght@0,100..900;1,100..900&display=swap");

// Base
$black: #1a1a1a; // Softened black
$white: #fafafa; // Warm white

// Grey shades (Film grain warmth)
$grey-100: #f5f5f5;
$grey-200: #e0e0e0;
$grey-300: #c6c6c6;
$grey-400: #a8a8a8;
$grey-500: #8a8a8a;
$grey-600: #6c6c6c;
$grey-700: #4e4e4e;
$grey-800: #363636;
$grey-900: #242424;

// Accent (Pastel neon - Think Osaka sunsets)
$accent-100: #fff0f5; // Blush
$accent-200: #ffcce6; // Cotton candy
$accent-300: #ff99cc; // Strawberry milk
$accent-400: #ff66b3; // Sakura pink
$accent-500: #ff3399; // Retro magenta (softened)
$accent-600: #e6007e; // Deeper pink
$accent-700: #cc0066; // Berry
$accent-800: #99004d; // Muted wine
$accent-900: #660033; // Velvet

// Primary (Muted tech blues - Like old Toshiba logos)
$primary-100: #f0f9ff;
$primary-200: #cceeff;
$primary-300: #99d6ff; // Sky blue
$primary-400: #66b3ff; // Soft azure
$primary-500: #3385ff; // Denim
$primary-600: #0066cc; // Retro blue
$primary-700: #004d99; // Dusk
$primary-800: #003366; // Deep ocean
$primary-900: #001a33; // Night

// Secondary (Warm amber - Film light leaks)
$secondary-100: #fff9e6;
$secondary-200: #ffedb3;
$secondary-300: #ffe080; // Honey
$secondary-400: #ffd147; // Mustard
$secondary-500: #ffc000; // Amber (like Fujifilm tones)
$secondary-600: #e6a800; // Gold
$secondary-700: #cc8f00; // Vintage yellow
$secondary-800: #997700; // Ochre
$secondary-900: #664d00; // Brown

// Bonus: Analog Film Colors
$film-cyan: #a5d8e7; // Faded cyan
$film-magenta: #e7a5c4; // Dusty pink
$film-yellow: #e7d8a5; // Sepia

// Typography (Soft rounded fonts)
$font-primary: "Zen Kaku Gothic New", "Hiragino Kaku Gothic Pro", sans-serif; // Friendly, readable
$font-secondary: "Yomogi", "Hannotate SC", cursive; // Handwritten warmth

// Borders (Subtle strokes)
$border-primary: 1px solid rgba($primary-500, 0.3); // Translucent
$border-accent: 1px dashed rgba($accent-500, 0.4); // Soft dashed
$border-radius-sm: 6px; // Gentle curves
$border-radius-md: 12px;
$border-radius-lg: 24px;

// Shadows
$shadow-sm: 0 1px 3px rgba($black, 0.12);
$shadow-md: 0 4px 6px rgba($black, 0.1);
$shadow-lg: 0 10px 15px rgba($black, 0.1);
$shadow-xl: 0 20px 25px rgba($black, 0.1);
$shadow-accent: 0 0 15px rgba($accent-500, 0.4);

// Transitions
$transition-fast: 0.15s ease-in-out;
$transition-medium: 0.3s ease-in-out;
$transition-slow: 0.5s ease-in-out;

// Breakpoints (Same, but softer transitions)
$breakpoint-mobile: 480px;
$breakpoint-tablet: 768px;
$breakpoint-desktop: 1024px;

/*Assets*/

.film-grain {
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%' height='100%' filter='url(%23noiseFilter)' opacity='0.1'/%3E%3C/svg%3E");
}

// Modern SCSS Reset
// A minimal, modern CSS reset for consistent cross-browser styling

// Use a more intuitive box-sizing model
*,
*::before,
*::after {
  box-sizing: border-box;
}

// Remove default margins and padding
* {
  margin: 0;
  padding: 0;
}

// Prevent font size inflation on mobile
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

// Set core body defaults
body {
  min-height: 100vh;
  line-height: 1.5;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Balance text wrapping on headings
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
  line-height: 1.1;
}

// Improve readability for paragraphs
p {
  text-wrap: pretty;
  max-width: 75ch;
}

// Remove list styles where appropriate
ul[role="list"],
ol[role="list"] {
  list-style: none;
}

// Make images easier to work with
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

// Inherit fonts for inputs and buttons
input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
}

// Remove button styles
button {
  background: none;
  border: none;
  cursor: pointer;
}

// Ensure textareas don't resize beyond their container
textarea {
  resize: vertical;
}

// Remove default table spacing
table {
  border-collapse: collapse;
  border-spacing: 0;
}

// Improve focus visibility
:focus-visible {
  outline: 2px solid currentColor;
  outline-offset: 2px;
}

// Remove focus outline for mouse users
:focus:not(:focus-visible) {
  outline: none;
}

// Reduce motion for users who prefer it
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

// Improve scrolling behavior
@media (prefers-reduced-motion: no-preference) {
  html {
    scroll-behavior: smooth;
  }
}

// Make sure hidden elements are truly hidden
[hidden] {
  display: none !important;
}

h1 {
  display: none;
}
